
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import moment from 'moment'
export default {
directives: {
TransferDom,
},
components: {
XButton,
Group,
FormPreview,
Datetime,
XDialog
},
data(){
return{
list:'',//课程信息
studentTime:moment().format('YYYY-MM-DD HH:mm:ss'),
teacherTime:moment().format('YYYY-MM-DD HH:mm:ss'),
studentDialog:false,
teacherDialog:false,
}
},
created(){
this.getlist()
},
methods:{
getlist(){
this.$http.get('http://139.199.168.158:8080/admin/coursecheck?eventid='+this.$route.query.id).then((response) => {
response = response.data;
if (response.code === 1) {
this.list = response.data[0]
console.log("获取课程信息成功")
} else {






console.log("获取课程信息失败")
}
});

},
handelStudent(){
this.$http.post('http://139.199.168.158:8080/admin/studentsign', {
id : this.list.id,
time : moment(this.studentTime).format('YYYY-MM-DD HH:mm:ss')
}).then((response) => {
response = response.data;
if (response.code === 1) {
console.log("学生签到成功")
this.studentDialog = false
this.getlist()
} else {






console.log("学生签到失败")
}
});
},
handelTeacher(){
this.$http.post('http://139.199.168.158:8080/admin/teachersign', {
id : this.list.id,
time : moment(this.teacherTime).format('YYYY-MM-DD HH:mm:ss')
}).then((response) => {
response = response.data;
if (response.code === 1) {
console.log("教师签到成功")
this.teacherDialog = false
this.getlist()
} else {






console.log("教师签到失败")
}
});
},
handelSupervisor(){
this.$http.post('http://139.199.168.158:8080/admin/supervisorsign', {
id : this.list.id,
time : moment().format('YYYY-MM-DD HH:mm:ss')
}).then((response) => {
response = response.data;
if (response.code === 1) {
console.log("督导签到成功")
this.getlist()
} else {






console.log("督导签到失败")
}
});
},


}
}



